* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  padding-top: 100px;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
}

.success {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 1.3rem;
  border: none;
  color: rgb(42, 172, 42);
  text-align: center;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header input {
  outline: none;
  border: none;
  box-shadow: 0 3px 2px -2px grey;
  font-size: 1.5rem;
  margin-bottom: 70px;
}

.header input:focus {
  outline: none;
  border: none;
  box-shadow: 0 3px 5px -2px grey;
}

.container {
  display: flex;
  align-items: center;
  outline: none;
  border: none;
  font-size: 1.5rem;
  margin-bottom: 10px;
}

ul{
  list-style-type: none;
}

.remove-button {
  border: none;
  background-color: transparent;
  color: hsl(330, 100%, 50%);
  font-weight: bold;
  font-size: 1.5em;
  cursor: pointer;
  transition: 0.5s ease;
  padding: 10px 10px;
  border-radius: 5px;
}

button:hover {
  border-color: red;
  background-color: hsla(283, 100%, 52%, 0.363);
}

button:focus,
button:focus-visible {
  outline: 4px auto orange;
}

li{
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(74, 28, 28, 0.25);
  padding: 10px;
}

li p {
  text-transform: capitalize;
  margin-left: 20px;
}

h1 {
  font-size: 3.2em;
  line-height: 1;
}

input[type=checkbox] {
  transform: scale(2);
}

li span {
  font-size: 14px;
  font-family: sans-serif;
  font-style: italic;
}

